@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(243, 209, 163);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.footer {
  background-image: url("http://rmcigarfestival.com/wp-content/uploads/2012/03/Beer-Banner.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: fixed;
  bottom: 0;
  z-index: 1000;
  width: 100%;
  border-bottom: 0.3em solid black;
  margin-top: 4em;
}

.footer_container {
  display: flex;
  flex-direction: row;
}

.footer_container a {
  margin: 0.4em;
  width: 100%;
  color: black;
  font-size: 1.4em;
}
.footer_container a:hover {
  cursor: pointer;
}

.footer_container li {
  list-style-type: none;
}

.app_download_container li {
  list-style-type: none;
  margin: 0.2em;
}

.app_download_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-left: 3em;
}
.app_download_container a {
  font-size: 1.4em;
}

.app_download_container svg:hover {
  cursor: pointer;
}

.download_title {
  line-height: 1.5;
  margin-right: 1em;
  font-weight: 800;
  width: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.navbar {
  background-image: url("http://rmcigarfestival.com/wp-content/uploads/2012/03/Beer-Banner.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
  width: 100%;
  border-bottom: 0.3em solid black;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 80px;
  margin-right: 2em;
}

.container {
  z-index: 1;
  width: 100%;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 0.5rem;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  justify-content: flex-end;
}

.nav-item {
  height: 80px;
  border-bottom: 2px solid transparent;
}

.nav-links {
  color: #000;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  font-weight: 800;
}

.nav-links:hover {
  color: #0473f2;
}

/* .activated {
  color: #0473f2;
} */

.fa-bars {
  color: #000;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 660px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    width: 100%;
  }

  .nav-menu.active {
    background: whitesmoke;
    left: 0;
    opacity: 1;
    transition: all 0.3s ease;
    z-index: 1;
    flex-direction: column;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    color: black;
  }

  .nav-links:hover {
    color: aquamarine;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: all 0.3s ease;
  }

  .nav-item:hover {
    border: none;
  }

  .nav-item {
    width: 100%;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
    height: 2em;
    width: 100%;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
  .avatar {
    margin: 1em;
    height: 3.2em;
    border-radius: 40px;
    width: 30%;
  }
}

.menu-icon svg {
  color: #000 !important;
}

.avatar {
  margin: 1em;
  height: 3.2em;
  border-radius: 40px;
  width: 5em;
}

.avatar:hover {
  cursor: pointer;
}

.navbar ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin-top: 1em;
  width: 100%;
}



